@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;400;500;700;800&display=swap);
.titleFontSize {
  font-size: 2rem;
}

@media screen and (min-width: 550px) {
  .titleFontSize {
    font-size: 3rem;
  }
}

.smolSpace {
  margin-right: 0.1em;
}

.title {
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.header {
  margin: 0px;
}

.subHeader {
  font-size: 1.5rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: var(--Black50);
  margin-top: 0px;
}

.RPButton {
  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: var(--CornerRadius);
  padding: 1em;
  border-color: var(--Black10);
  border-style: none;
  transition: var(--TransitionTime);
}

.RPButton:hover {
  background-color: var(--Black80);
  transition: var(--TransitionTime);
  cursor: pointer;
}

.aboutContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row;
}

.imgContainer {
  margin-right: 2em;
}

.contentContainer {
  text-align: left;
}

.selfie {
  --imgDimension: 12em;
  height: var(--imgDimension);
  width: var(--imgDimension);
  padding: 2em;
  border-radius: var(--CornerRadius);

  --outline-size: 2px;
  outline: var(--outline-size) solid white;
  outline-offset: calc(var(--outline-size) * -1);

  aspect-ratio: 4000/4000;
  background-size: cover;
  background-image: url(/static/media/goodPhoto.2677ec65.jpg);
}

@media screen and (max-width: 750px) {
  .imgContainer {
    margin-right: 0em;
  }

  .aboutContainer {
    flex-flow: column;
  }

  .contentContainer {
    text-align: center;
  }
}

.appContainer {
  /* background-color: purple; */
}

.headerContainer {
  width: min(1280px, 80%);
  height: 100%;
  display: flex;
  align-items: center;
}

.titleContainer {
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: left;
}

.projectsContainer {
  width: min(1280px, 80%);
  height: 100%;
  display: flex;
  align-items: center;
  flex-flow: column;
}

.downArrowContainer {
  cursor: pointer;
}

.fadeInArrow {
  opacity: 0;
  -webkit-animation: fadein ease-in var(--FadeTime) forwards;
          animation: fadein ease-in var(--FadeTime) forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  position: fixed;
  top: 95%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.fadeOutArrow {
  opacity: 1;
  -webkit-animation: fadeout ease-in var(--FadeTime) forwards;
          animation: fadeout ease-in var(--FadeTime) forwards;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  position: fixed;
  top: 95%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.downArrow {
  color: white;
  font-size: 5rem;
  cursor: pointer;
}

.view1 {
  /* background-color: green; */
  height: 100vh;
  width: 100vw;

  display: flex;
  justify-content: center;
}

.view2 {
  /* background-color: blue; */
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;

  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.view3 {
  /* background-color: red; */
  height: 100vh;
  width: 100vw;

  display: flex;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.RPCard {
  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: var(--CornerRadius);
  padding: 2em;
  cursor: pointer;
  box-sizing: border-box;

  transition: var(--TransitionTime);

  height: var(--CardHeight);
  width: 100%;
  display: flex;
  align-self: center;
}

.cardContentContainer {
  width: 100%;
  display: flex;
  align-items: center;
}

.cardImage {
  height: 100%;
  width: -webkit-max-content;
  width: max-content;
  margin-right: 2em;
  box-shadow: 1px 2px 8px var(--Black);
}

@media screen and (max-width: 750px) {
  .RPCard {
    height: var(--CardHeightNarrow);
  }

  .cardContentContainer {
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  .cardImage {
    height: auto;
    width: auto;
    max-width: 400px;
    max-height: 300px;
    margin-right: 0px;
  }
}

@media screen and (max-width: 580px) {
  .cardImage {
    height: auto;
    width: auto;
    max-width: 256px;
    max-height: 256px;
    margin-right: 0px;
  }
}

.openGLPathfinderCard {
  background-color: #626262cc;
}

.openGLPathfinderCard:hover {
  background-color: #626262aa;
}

.rayTracerCard {
  background-color: #6b0025cc;
}

.rayTracerCard:hover {
  background-color: #6b0025aa;
}

.lifeOfAProgrammer {
  background-color: rgba(151, 31, 255, 0.4);
}

.lifeOfAProgrammer:hover {
  background-color: rgba(151, 31, 255, 0.3);
}

.vpeqCard {
  background-color: rgba(100, 100, 100, 0.4);
}

.vpeqCard:hover {
  background-color: rgba(100, 100, 100, 0.3);
}

/* --- Globals --- */

:root {
  --Primary: #a11d6a;
  --Secondary: #951fff;
  --Linear: linear-gradient(to right, var(--Primary), var(var(--Secondary)));
  --Error: #e00248;

  --Black: #262626;
  --Black90: #333333;
  --Black80: #444444;
  --Black75: #666666;
  --Black50: #a6a6a6;
  --Black25: #e6e6e6;
  --Black10: #f2f2f2;

  --CornerRadius: 0.5em;

  --CardHeight: 160px;
  --CardHeightNarrow: 512px;
  --CardWidth: min(1280px, 100%);

  --TransitionTime: 0.2s;
  --FadeTime: 0.5s;

  --TextMargin: 1em;
}

.whiteOutline {
  --outline-size: 2px;
  outline: var(--outline-size) solid white;
  outline-offset: calc(var(--outline-size) * -1);
}

.greyOutline {
  --outline-size: 2px;
  outline: var(--outline-size) solid #666666;
  outline: var(--outline-size) solid var(--Black75);
  outline-offset: calc(var(--outline-size) * -1);
}

.marginTop2em {
  margin-top: 2em;
}

.Monospace {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.Paragraph,
p {
  font-size: 0.9rem;
  font-weight: 100;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.h1 {
  font-size: 7.063rem;
  font-weight: 600;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.h2 {
  font-size: 5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h3 {
  font-size: 3rem;
  font-weight: 1000;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h4 {
  font-size: 2.5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h5 {
  font-size: 1.75rem;
  font-weight: 200;
  font-family: "Poppins", sans-serif;
  color: #a6a6a6;
  color: var(--Black50);
}

.h6 {
  font-size: 1rem;
  font-weight: 100;
  font-family: "Poppins", sans-serif;
  color: #666666;
  color: var(--Black75);
}

html,
body {
  background: #262626;
  scrollbar-width: none;
  scroll-behavior: smooth;
  /* height: calc(100vh - 40px); */
  margin: 0px;
}

* {
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

